/* eslint-disable no-unused-vars */
import moment from 'moment';
import React from 'react';
import { useSelector } from 'react-redux';
import { currencyFormat } from '../../../utils/helper/utilities';
import useCalculateSales from '../closing-shift/hooks/useCalculateSales';
import RowItemPrint from '../widget/RowItemPrint';

export const ReportPrint = React.forwardRef(({ titlePrint = '', ...props }, ref) => {
	const { cashier = {}, property = {}, user = {} } = JSON.parse(localStorage.getItem('app') || '{}');
	const { isClosing = false } = cashier;
	const { reportSalesShift = {}, loadingReportShift = false } = useSelector((state) => state?.report);
	const { itemGroup = [], itemCategory = [], items = [], balance = [], settlement = [] } = reportSalesShift;
	const { getBalance } = useCalculateSales();

	return (
		<div className="w-full p-3 py-5 bg-white rounded-md mx-auto" style={{ maxWidth: '350px' }} ref={ref}>
			<div>
				<p className="text-center text-print-medium font-bold">{property?.header1 || ''}</p>
				<p className="text-center text-print-small font-semibold">{property?.header2 || ''}</p>
				<p className="text-center text-print-xsmall mt-2">{property?.header3 || ''}</p>
				<p className="text-center text-print-xsmall">{property?.header4 || ''}</p>
				<p className="text-center text-print-xsmall">{property?.header5 || ''}</p>
				<p className="text-center text-print-xsmall">{property?.header6 || ''}</p>
			</div>
			<div className="my-3 w-full border border-separate border-gray-400" />
			<div>
				<p className="text-center text-print-medium mt-3 font-semibold">{titlePrint?.toUpperCase()}</p>
			</div>
			<div className="my-3 w-full border border-separate border-gray-400" />
			<div>
				<RowItemPrint marginRow="my-1" className="text-print-xsmall" title="nama kasir" value={user?.username} />
				<RowItemPrint
					marginRow="my-1"
					className="text-print-xsmall"
					title="opening shift"
					value={moment(cashier?.shiftDate).format('dddd, DD MMMM YYYY')}
				/>
				<RowItemPrint
					marginRow="my-1"
					className="text-print-xsmall"
					title="subtotal"
					value={'IDR ' + currencyFormat(getBalance('SubTotal'), 0)}
				/>
				<RowItemPrint
					marginRow="my-1"
					className="text-print-xsmall"
					title="sales discount"
					value={'IDR ' + currencyFormat(getBalance('DiscValue'), 0)}
				/>
				<RowItemPrint
					marginRow="my-1"
					className="text-print-xsmall"
					title="discount bill"
					value={'IDR ' + currencyFormat(getBalance('DiscBill'), 0)}
				/>
				<RowItemPrint
					marginRow="my-1"
					className="text-print-xsmall"
					title="rounded"
					value={'IDR ' + currencyFormat(getBalance('Rounded'), 0)}
				/>
				<RowItemPrint
					marginRow="my-1"
					className="text-print-xsmall"
					title="service tax"
					value={'IDR ' + currencyFormat(getBalance('ServValue'), 0)}
				/>
				<RowItemPrint marginRow="my-1" className="text-print-xsmall" title="tax" value={'IDR ' + currencyFormat(getBalance('TaxValue'), 0)} />
				<RowItemPrint
					marginRow="my-1"
					className="text-print-xsmall"
					title="grand total"
					value={'IDR ' + currencyFormat(getBalance('GrandTotal'), 0)}
				/>
				<RowItemPrint
					marginRow="my-1"
					className="text-print-xsmall"
					title="net sales"
					value={'IDR ' + currencyFormat(getBalance('NetSales'), 0)}
				/>
				<RowItemPrint
					marginRow="my-1"
					className="text-print-xsmall"
					title="sales collected"
					value={'IDR ' + currencyFormat(getBalance('SalesCollected'), 0)}
				/>
			</div>
			<div className="my-3 w-full border border-separate border-gray-400" />
			{itemGroup?.map(({ groupCode = '', groupName = '', grossSales = 0, salesDiscount = 0, nettSales = 0, servValue = 0, taxValue = 0 }) => {
				return (
					<div key={groupCode}>
						<h1 className="text-center text-print-small font-semibold uppercase">TOTAL {groupName.toUpperCase() || 'group'}</h1>
						<div className="my-3 w-full border border-separate border-gray-400" />
						<RowItemPrint
							marginRow="my-1"
							className="text-print-xsmall"
							title="Total Gross Sales"
							value={`IDR ${currencyFormat(grossSales, 0)}`}
						/>
						<RowItemPrint
							marginRow="my-1"
							className="text-print-xsmall"
							title="Total Sales Discount"
							value={`IDR ${currencyFormat(salesDiscount, 0)}`}
						/>
						<RowItemPrint
							marginRow="my-1"
							className="text-print-xsmall"
							title="Total Service Tax"
							value={`IDR ${currencyFormat(servValue, 0)}`}
						/>
						<RowItemPrint marginRow="my-1" className="text-print-xsmall" title="Total Tax" value={`IDR ${currencyFormat(taxValue, 0)}`} />
						<RowItemPrint
							marginRow="my-1"
							className="text-print-xsmall"
							title="Total Nett Sales"
							value={`IDR ${currencyFormat(nettSales, 0)}`}
						/>
						<div className="my-3 w-full border border-separate border-gray-400" />
					</div>
				);
			})}
			<div>
				<div className="w-full mb-3">
					<h1 className="text-center text-print-small font-semibold uppercase">TOTAL SETTLEMENTS</h1>
				</div>
				<div className="my-3 w-full border border-separate border-gray-400" />

				{settlement?.map(({ CardName = '', PaidValue = 0 }) => {
					return (
						<RowItemPrint
							marginRow="my-1"
							className="text-print-xsmall"
							title={CardName.trim()}
							value={`IDR ${currencyFormat(PaidValue, 0)}`}
						/>
					);
				})}
			</div>
		</div>
	);
});
